.press-card {

  $block-name:                &; // #{$block-name}__element

  border: 1px solid #dddfe2;
  border-radius: 3px;
  background: #fff;
  padding: 20px;
  text-align: center;

  &__link {
    color: #427eec;

    &:hover {
      text-decoration: none;
    }
  }
}
