.clients {

  $block-name:                &; // #{$block-name}__element

  @include row;

  &__header {
    @include col();
    @include col(lg, 4);
    margin-bottom: 20px;
  }

  &__body {
    @include col();
    @include col(lg, 8);
  }
}
